/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.decors, .intro::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.header, .header__navbar, .footer__menu, .footer__socials, .footer__socials > li > a {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.container {
  display: block;
  width: 100%;
  max-width: 1136px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
}

.container--no-gutters {
  padding-right: 0;
  padding-left: 0;
}

.container--no-gutters .item-group {
  margin-right: 0;
  margin-left: 0;
}

.container--no-gutters .item {
  padding-right: 0;
  padding-left: 0;
}

.container .item-group {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.container .item {
  padding-right: 15px;
  padding-left: 15px;
}

.index {
  display: none;
  padding-right: 0.6rem;
  padding-left: 0.6rem;
  border-bottom: 2px solid #212021;
  font-family: "Lato", sans-serif;
  font-size: 0.875rem;
  font-weight: 300;
  font-style: italic;
  line-height: 2;
  pointer-events: none;
}

@media (min-width: 1200px) {
  .index {
    display: inline-block;
  }
}

.screen {
  position: relative;
  overflow: hidden;
}

.screen__separator {
  width: 2px;
  height: 36px;
  margin-right: auto;
  margin-left: auto;
  background-color: #cb98a8;
}

.screen__index {
  position: absolute;
  left: 3.85%;
}

.screen__socials {
  position: absolute;
  right: 3.5%;
  -webkit-writing-mode: vertical-lr;
      -ms-writing-mode: tb-lr;
          writing-mode: vertical-lr;
}

.logo {
  display: block;
}

.link {
  display: inline-block;
  position: relative;
  font-family: "Myriad Pro", sans-serif;
  font-size: 0.875rem;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 1.5px;
  color: #212021;
  /**
   * HACK: Делаем таким образом нижнюю границу для эффекта 
   * плавного выдвижения
   */
}

.link::before {
  background-color: #212021;
}

@media (min-width: 768px) {
  .link {
    font-size: 1rem;
  }
}

.link:hover {
  color: inherit;
}

.link:hover::before {
  width: 0;
}

.link::before {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: -6px;
  left: 0;
  transition: width 0.15s ease-in-out;
}

.link::after {
  content: "--------";
  margin-right: 0.8rem;
  margin-left: 0.2rem;
  letter-spacing: -2px;
  white-space: nowrap;
}

.title {
  position: relative;
  margin-top: 2.5rem;
  font-weight: 700;
  text-align: center;
  letter-spacing: 4px;
}

@media (min-width: 768px) {
  .title {
    margin-top: 6rem;
  }
}

.title::before, .footer__menu::before {
  content: "";
  display: none;
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  color: #f8f8f8;
  font-size: 6rem;
  font-weight: 700;
  letter-spacing: 30px;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .title::before, .footer__menu::before {
    display: inline;
  }
}

.socials {
  display: none;
  margin-bottom: 0;
}

@media (min-width: 1200px) {
  .socials {
    display: flex;
    flex-wrap: wrap;
  }
}

.socials__item {
  color: #212021;
  font-size: 0.875rem;
  font-weight: 300;
  letter-spacing: 1.2px;
}

.socials__item + .socials__item::before {
  content: "\2014";
  margin-right: 0.25rem;
  margin-left: 0.25rem;
}

.socials__link {
  color: inherit;
  text-transform: uppercase;
  text-decoration: none;
  transition: color 0.15s ease-in-out;
}

.socials__link:hover {
  color: #cb98a8;
}

.decors {
  z-index: -1;
  overflow-x: hidden;
}

.decors__container {
  height: 100%;
  position: relative;
  max-width: 1108px;
  background-image: linear-gradient(90deg, #f8f8f8 0%, #f8f8f8 0.2%, transparent 0.2%, transparent 99.8%, #f8f8f8 99.8%, #f8f8f8 100%);
}

.decors__container::before, .decors__container::after {
  content: "";
  display: none;
  width: 2px;
  height: 100%;
  position: absolute;
  z-index: -1;
  top: 0;
}

@media (min-width: 768px) {
  .decors__container::before, .decors__container::after {
    display: block;
  }
}

.decors__container::before {
  left: 33.25%;
}

.decors__container::after {
  left: 66.5%;
}

.decors__item {
  display: none;
  position: absolute;
  z-index: 1;
}

@media (min-width: 992px) {
  .decors__item {
    display: block;
  }
}

.img {
  width: 100%;
  height: auto;
}

.menu-icon {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 20px;
  height: 12px;
  margin-right: auto;
  margin-left: auto;
  cursor: pointer;
}

.menu-icon__bar {
  width: 100%;
  height: 2px;
  background-color: #212021;
  transition: width 0.25s ease-in-out, transform 0.25s ease-in-out;
}

.menu-icon__bar--first {
  width: 85%;
}

.menu-icon__bar--third {
  width: 60%;
}

.menu-icon--active .menu-icon__bar {
  width: 100%;
}

.menu-icon--active .menu-icon__bar--first {
  transform: translate3d(0, 5px, 0) rotate(45deg);
}

.menu-icon--active .menu-icon__bar--second {
  width: 0;
}

.menu-icon--active .menu-icon__bar--third {
  transform: translate3d(0, -5px, 0) rotate(-45deg);
}

.header {
  width: 100%;
  position: absolute;
  z-index: 10;
}

.header__img-wrap {
  display: block;
